.chatManager {
    padding: 20px;
    margin: 0 auto;
}

.chatManager h2 {
    color: #333;
    margin-bottom: 20px;
    text-align: center;
}

.chatManager select {
    width: calc(100% - 20px);
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
}

.chatManager input[type="text"] {
    width: calc(100% - 20px);
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
}

.chatManager button {
    width: calc(100% - 20px);
    padding: 10px;
    background-color: #ffd800;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 20px;
}

.chatManager button:hover {
    background-color: #ffdb4d;
}

.chatList {
    padding: 20px;
}

.room_grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
}

.room_area {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
}

.room_area img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 50%;
    margin-bottom: 30px;
}

.room_area input {
    margin-bottom: 10px;
    padding: 5px;
    width: 100%;
    box-sizing: border-box;
}

.join_btn {
    padding: 10px;
    background-color: #ffd800;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
}

.join_btn:hover {
    background-color: #ffdb4d;
}

.delete_btn {
    padding: 10px;
    background-color: #ff0000;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    margin-top: 10px;
}

.delete_btn:hover {
    background-color: #ff3333;
}